
.vimeography-thumbnail {
  width: 100%;
  margin: 0;
  border-bottom: 1px solid #333;
  outline: 0;
}

.vimeography-link {
  display: flex;
  align-items: flex-start;
  outline: 0;
  box-shadow: none;
  padding: 10px;
  background-color: #444;
  text-decoration: none;
  transition: all 0.25s ease-in-out;
}

.vimeography-title {
  transition: all 0.25s ease-in-out;
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 1rem;
  margin: 0;
  padding: 0 0 5px;
  color: #ddd;
}

.vimeography-description {
  transition: all 0.25s ease-in-out;
  font-size: 0.7rem;
  line-height: 1.2em;
  margin: 0;
  padding: 0;
  color: #bbb;
}

.vimeography-link:hover,
.vimeography-link-active {
  background-color: #555;
  outline: 0;
  box-shadow: none;

  .vimeography-title {
    color: #fff;
  }

  .vimeography-description {
    color: #ddd;
  }
}

.vimeography-thumbnail-image-wrapper {
  flex: 0 0 20%;
}

.vimeography-thumbnail-img {
  max-width: 100%;
  border-radius: 2px;
  align-self: center;
  box-shadow: none;
}

.vimeography-info {
  flex: 0 0 80%;
  padding: 0 10px;
  box-sizing: border-box;
}

/deep/ .vimeography-download {
  padding: 0 0 5px 10px;
}

@media all and (min-width: 940px) {
  .vimeography-thumbnail-image-wrapper {
    flex: 0 0 30%;
  }

  .vimeography-info {
    flex: 0 0 70%;
  }
}
